:root{
  --primary_col: brown;
  --secondary_col: green;
  font-family: 'Paula', sans-serif;
}

.marker{
    margin: 0;
    padding: 0;
    position: relative;
    overflow: hidden;
    transition:
        width .5s,
        height .5s;
    border-radius:10px;
    cursor: pointer;
}

.marker_name, .marker_info, .marker_infoExpanded{
    border-radius:10px; 
    padding: 10px;
    background-color: inherit;
     font-family:Paula, sans-serif;
     font-size: x-large;
     line-height: 1;
     color: inherit;
     top: 0;
     left: 0;
    position: absolute;
     display: inline-block;
   white-space: nowrap;
     box-sizing: border-box;
   }

   .marker_name {
    font-weight: bold;
    z-index: 2000; /* Ensure it's above the info */
  }
  
  .marker_info {
    transition: left 0.5s ease; /* Animate the width change */
    z-index: 1000; /* Below the name tag */
  }
  .marker_infoExpanded{
    width: inherit;
    text-wrap: wrap;
    text-align: justify;
  }

  .leaflet-marker-icon {
    background-color: transparent !important;
    border: none !important;
  }

  /*maingame stuff*/
  .maingame-mainMenu{
    font-family: 'Paula', sans-serif;
    background-color: var(--primary_col);
    color: var(--secondary_col);
    position: absolute;
    display: flex;
    justify-content: space-evenly;
    z-index: 1000;
    height: 4vh;
    width: 100vw;
    bottom: 0;
    font-size: large;
    font-weight: bold;
  }
  .maingame-menuCategory{
    display: flex;
    align-items: center;
  }
  .menuCategory-categoryPanels.closed{
    display: none;
  }
  .menuCategory-categoryPanels{
    display:grid;
    grid-auto-flow:column dense; /* column flow with "dense" to fill all the cells */
    grid-template-rows:12.5vh 12.5vh; 
    grid-auto-columns:15vw;
    position: absolute;
    top: -25vh;
    left: 50%;
    margin-right: -50%;
    transform: translate(-50%, 0%);
    background-color: inherit;

  }
  .maingame-categoryPanel{
    border-radius:10px;
    display: grid;
    grid-template-rows:50% 50%; 
    background-color: var(--primary_col);
    justify-content: center;
    align-content: center;
    align-items: center;
    flex-wrap: wrap;
    flex-direction: column;
    justify-items: center;
  }
  .categoryPanel-name{
    color: var(--secondary_col);
    text-align: center;
    font-size: 2vh;
  }
  .categoryPanel-value{
    font-size: xx-large;
    background-color: var(--secondary_col);
    color: var(--primary_col);
    width: auto;
      padding-inline: 1vw;
      border-radius:10px;
    text-align: center;
    margin-bottom: .5vh; font-weight: bold;
  }
  .categoryPanel-expandableDetails{
    background-color: var(--primary_col);
    padding-inline: 5%;
    position: absolute;
    bottom: 25vh;
    left: 0;
  }
  .expandableDetails-description{
    font-size: medium;
    font-weight: normal;
    text-align: justify;
  }
  .graph-outer-container{
    color: var(--primary_col);
    background-color: var(--secondary_col);
    border-radius:10px;
    margin-block-end:5%
    }